<template>
  <div class="not-found">
    <header class="not-found__header border-bottom">
      <i @click="$router.replace('/home')" class="not-found__header-img not-found__header-img--logo"></i>
      <span class="not-found__header-line border-right"></span>
      <i class="not-found__header-img not-found__header-img--partner"></i>
    </header>
    <div class="not-found__content">
      <i class="not-found__img"></i>
      <p class="not-found__tips not-found__tips--cn">
        尊敬的客户，很抱歉您访问的页面无法连接。如您对此有任何疑问，可联系我们的客服中心。
      </p>
      <p class="not-found__tips not-found__tips--en">
        The requested page could not be found. Should you have any enquiries regarding this matter, please contact our
        customer service center.
      </p>
      <p class="not-found__tips not-found__tips--timer">
        <span class="monospaced-font">{{this.count}}</span>s后自动返回
        <a class="not-found__link-home" @click="$router.replace('/')" href="javascript:void (0);">首页</a>
      </p>
      <div class="not-found__links-wrapper">
        <a class="not-found__link-email border" href="javascript:void (0);">
          <p>发送电子邮件</p>
          <p>cs@k8.com</p>
        </a>
        <a class="not-found__link-service border" href="javascript:void (0);">
          <p>在线客服</p>
          <p>LIVE CHAT</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'not_found',
  data() {
    return {
      count: 5,
      timer: null
    }
  },
  created() {
    this.timer = setInterval(() => {
      if (this.count > 1) {
        this.count--
      } else {
        this.$router.replace('/')
      }
    }, 1000)
  },
  destroyed() {
    clearInterval(this.timer)
  }
}
</script>

